.associated-tabs {
	&.tabs {
		border-width: 0 0 0;
	}

	.tabs__items {
		padding: 4px 0 0 8px;
	}

	.tabs__item__nav {
		font-weight: $text-weight-semi-bold;
		font-size: 14px;
		line-height: 21px;
		color: $app-form-label-color;
		border: 1.5px solid $border-light-color;
		box-shadow: 0px 4px 20px rgba(224, 21, 21, 0.05);
		border-radius: 8px;
		padding: 8px;
		margin-bottom: 18px;
		min-width: 140px;
		align-items: center;
		text-align: center;

		.tab-title {
			margin-left: 8px;
			text-align: center;
		}

		.app-icon {
			width: 16px;
			height: 16px;
			margin-bottom: -4px;
		}
	}

	.empty-states {
		text-align: center;
		margin-top: 88px;
		.empty-state-img {
			margin-bottom: 12px;
		}
	}
}
