.itom-service-create-form-container {
	.itom-service-create-form {
		//Froala editor upload image hided. Img upload not allowed for Assets and Service
		.fieldset-wrapper .textarea-field[formserv-field-name="description"] .fr-toolbar .fr-command[data-cmd="insertImage"] {
			display: none;
		}

		.section-title {
			font-weight: $text-weight-semi-bold;
			@include font-size(16px);
		}

		.select-field[formserv-field-name="groupId"],
		.select-field[formserv-field-name="agentId"] {
			@include flexbox();
			@include flex-direction(column);

			.form-hint-custom {
				@include font-size(12px);
				margin-top: 4px;
				order: 6;
			}
		}

		.avatar-select-option .avatar-option .avatar__icon--xsmall {
			margin: 0 8px 0 0;
		}

		.avatar-option {
			@include flexbox;
			@include align-items(center);

			.avatar__icon--xsmall {
				margin: 0 8px;

				.avatar__initial {
					height: 24px;
					width: 24px;
				}
			}
		}
	}

	.attach-files {
		margin-left: 0px;
		margin-right: 0px;
		.attachment-list .list-file-item {
			width: 100%;
		}
	}
}
