.whos-oncall-now-modal {
	.title {
		padding: 10px 0 10px;
	}
	.onCallModelGroup {
		margin-bottom: 12px;
		width: 428px;
		.ember-power-select-selected-item {
			width: 400px;
		}
	}
	.no-oncall-members {
		border: 1px solid $app-border-primary;
		height: 420px;
		margin-top: 38px;
		text-align: center;
		border-radius: $radius;
		.no-members-img {
			margin-top: 168px;
		}
		.no-members-text {
			text-align: center;
			margin-top: 8px;
		}
	}
	.no-border {
		border: none;
	}
	.oncall-agent {
		padding: 16px;
		border: 1px solid $border-dark-color;
		border-radius: 2px;
		.heading {
			font-weight: $text-weight-semi-bold;
		}
		.content {
			padding: 12px;
			border: 1px solid $border-light-color;
			margin-top: 14px;
			@include flexbox();
			border-radius: $inputBorderRadius;
			background: $background-color-2;
			.avatar-image {
				width: 40px;
				margin: 24px 16px 0 8px;
			}
			.agent-detail {
				margin-bottom: 6px;
				@include flexbox();
				.shift-label {
					color: $secondary-text-color-3;
					width: 56px;
				}
				.shift-name {
					max-width: 316px;
					word-wrap: break-word;
				}
				.shift-value-bold {
					font-weight: $text-weight-semi-bold;
				}
				.time-range {
					@include font-size($text-normal);
				}
			}
		}
	}
	.ember-modal-body {
		.tabs {
			border: none;
		}
	}
}
