.itom-associations-tab-container {
	//TODO: Need to add strong class name for severity and need to remove duplicate import
	@import "common";
	@import "association-modal";
	@import "associated-incident";
	margin-top: 24px;
	.itom-associated-alert-list {
		@include flexbox();
		border: 1px solid $border-light-color;
		box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.07);
		border-radius: $radius;
		margin-top: 8px;

		.alert-img-icon {
			border: 1px solid $border-light-blue;
			border-radius: $radius;
			background: $section-background-blue;
			height: 32px;
			width: 32px;
			@include flexbox-simple(center, center);
			margin: 20px 0 0 20px;

			svg {
				width: 16px;
				height: 16px;
			}
		}

		.alert-details {
			margin: 16px;
			width: calc(100% - 120px);

			.alert-info {
				font-weight: $text-weight-medium;
				background: none;
			}

			.alert-type {
				color: $secondary-text-color-1;
				margin-top: 4px;
			}
		}

		.alert-properties {
			width: 184px;
			text-align: left;
			min-width: 0;
			border-left: 1px solid $border-light-color;
			margin: 12px 0px;

			.alert-status,
			.alert-severity {
				margin-left: 16px;
				@include flexbox;
				@include align-items(center);
				margin-top: 2px;

				.tag {
					margin-right: 0px;
				}
			}

			.alert-status {
				padding-top: 3px;
			}

			.alert-status-label,
			.alert-severity-label {
				color: $app-form-label-color;
				flex: 2;
				padding-bottom: 0;
			}

			.alert-severity-indicatior {
				width: 10px;
				height: 10px;
				border-radius: 2px;
				display: inline;
			}
			@include set-severity-tags();

			.alert-status-data,
			.alert-severity-data {
				flex: 3;
				font-weight: $text-weight-medium;
				margin-right: 4px;
			}

			.ok {
				text-transform: uppercase;
			}
		}
	}
}
.associated-heading {
	display: flow-root;
	.associated-tab-title {
		font-weight: $text-weight-normal;
		color: $secondary-text-color-1;
		font-size: 12px;
	}
	.view-all {
		float: right;
		font-weight: $text-weight-semi-bold;
	}
}
