.itom_service_details_contracts {
	.itom_service_contracts {
		@include flexbox();
		border: 1px solid $border-light-color;
		box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.07);
		border-radius: $radius;
		margin-top: 8px;
		.contract-details {
			margin: 16px;
			width: calc(100% - 120px);
			.contract-info {
				font-weight: $text-weight-medium;
			}
			.contract-type {
				color: $secondary-text-color-1;
				margin-top: 4px;
			}
		}
		.contract-status {
			border-radius: 4px;
			padding: 4px 10px;
			margin: 24px;
			font-weight: $text-weight-semi-bold;
			background-color: $draftContract;
			&.approved, &.active {
				background: $statusNewBg;
				border: solid 1px $activeContract;
				color: $activeContract;
			}
			&.pending.approval {
				width: 160px;
				text-align: center;
			}
		}
	}
	.no-contracts {
		text-align: center;
		margin-top: 64px;
		.no-contracts-img {
			margin-bottom: 8px;
		}
	}
}
