&.services-details {
	@import "../admin/itom-ams/alert-banner";
	background: $app-white;
	.section-title-tabs {
		width: 100%;
		.tabs {
			margin-bottom: 0px;
			margin-top: 8px;
		}
	}
	.title-container {
		width: 100%;
		@include flexbox();
		margin-top: 8px;
		.head-icon {
			height: 38px;
			width: 38px;
			margin-left: 40px;
			margin-top: 30px;
			.head-svg {
				margin-top: 10px;
				margin-left: 12px;
				width: 14px;
			}
		}
		.empty-health-status {
			background: $alertCancelColor;
		}
		.title-loader {
			width: 360px;
			@include flexbox();
			margin-left: -92px;
		}
		.push-right {
			float: right;
			width: 310px;
			margin-top: 16px;
			.view-service-map-svg {
				margin-right: 12px;
				align-items: center;
			}
			.btn-more-dots {
				display: inline-block;
			}
			.ember-basic-dropdown-content {
				min-width: 110px;
				margin-right: 12px;
			}
		}
		.title-header {
			margin-left: 12px;
			margin-top: 24px;
			max-width: 70%;
			.title {
				font-weight: $text-weight-semi-bold;
				@include font-size($text-large);
				padding-bottom: 2px;
			}
			.content {
				@include flexbox();
				@include font-size($text-normal);
				.title-label {
					margin-right: 8px;
					color: $app-form-label-color;
				}
				.title-option {
					margin-right: 8px;
					font-weight: $text-weight-semi-bold;
				}
				.vertical-div {
					margin-right: 12px;
				}
			}
		}
	}
	.sticky-element {
		padding: 0;
		max-height: none;
		.sticky-element--sticky {
			background: $app-white;
		}
	}
	.page-title {
		background: $app-white;
		height: 100%;
		box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.07);
		border: 1px solid $border-dark-color;
		display: block;
	}
	.tabs__items {
		padding: 8px 0 0 20px;
	}
	.page-content {
		width: calc(100% - 344px - 16px);
		max-width: calc(100% - 300px);
	}
	#tabPaneContent {
		.alert-banner {
			.heading {
				color: $primary-text-color-1;
			}
			.next-btn {
				margin-left: auto;
			}
		}
		.overview_decription {
			background-color: $section-background-blue;
			line-height: 21px;
		}
		.itom_service_overview {
			@include flexbox();
			border: 1px solid $border-light-color;
			box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.07);
			border-radius: $radius;
			margin-top: 24px;
			.overview_content {
				padding: 16px 24px;
				width: 100%;
				word-wrap: break-word;
				.title {
					font-weight: $text-weight-semi-bold;
					font-size: $title-normal;
					margin-bottom: 12px;
				}
				.view-more-toggle {
					font-weight: $text-weight-semi-bold;
				}
				.associated-heading {
					.view-all {
						margin-top: -20px;
					}
				}
			}
		}
		@import "alert-configurations";
		.empty-states {
			text-align: center;
			margin-top: 28px;
			.empty-state-img {
				margin-bottom: 12px;
			}
			.empty-state-messages {
				margin-bottom: 48px;
			}
		}
		@import "activities";
	}
	.itom-service-details-sidebar {
		width: 352px;
		svg {
			width: 14px;
			height: 14px;
			margin-right: 4px;
			margin-bottom: -2px;
		}
		.view-oncall-members {
			font-weight: $text-weight-medium;
			@include font-size($text-normal);
			color: $sliderTrackFill;
			margin-top: 4px;
			margin-left: -4px;
		}
		.sidebar-content {
			padding: 16px 24px;
			overflow-y: auto;
			max-height: 400px;
			word-wrap: break-word;
			max-width: 272px;
			.title {
				font-weight: $text-weight-semi-bold;
				margin-bottom: 12px;
			}
			.sidebar-label {
				color: $app-form-label-color;
				margin-top: 12px;
				@include font-size($text-normal);
			}
			.sidebar-option {
				font-weight: $text-weight-semi-bold;
			}
			.view-more-less {
				font-weight: $text-weight-medium;
				color: $sliderTrackFill;
				margin-left: -4px;
			}
		}
		.divide-service-subtype {
			border-bottom: 1px $app-border-primary solid;
			padding-bottom: 12px;
			margin-bottom: 12px;
			&:last-child {
				border-bottom: none;
				padding-bottom: 0px;
			}
		}
		.itom-service-sidebar {
			border: 1px solid $border-light-color;
			box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.07);
			border-radius: $radius;
			margin: 20px 20px 12px 16px;
			background: $app-white;
			.loader-container {
				height: 168px;
			}
		}
		.agent-info {
			@include flexbox();
			.avatar__icon {
				min-width: 32px;
				min-height: 32px;
			}
			.agent-info-text {
				margin-left: 8px;
				word-wrap: break-word;
				max-width: 200px;
				.agent-update-info {
					@include font-size($text-normal);
					.oncall-details {
						color: $app-form-label-color;
					}
				}
			}
			.primary-oncall-div {
				border-bottom: 1px $app-border-primary solid;
				padding-bottom: 8px;
				margin-bottom: 8px;
				&:last-child {
					border-bottom: none;
					padding-bottom: 0px;
				}
			}
		}
	}

	.attachments {
		padding: 0;
		margin: 16px 0 0;
		background: none;
		.attachment-list .attach-files {
			padding: 0;
			margin: 0;
			background: none;
			.attachment-title {
				display: none;
			}
			.attachment-uploader {
				margin-bottom: 0;
			}
		}
		.list-file-item.cloud-file .file.cloud {
			.file-info {
				display: block;
				margin: 0;
			}
			.file-name {
				max-width: 100%;
			}
		}
	}

	.attachment-header-title {
		color: $app-grey;
		font-size: $text-normal;
		font-weight: $text-weight-medium;
		height: 24px;
		@include flexbox();
		align-items: center;
		span {
			@include flexbox();
		}
		.ember-tooltip {
			max-width: 210px;
			height: 32px;
		}
	}

	.attachment-header-icon {
		fill: $app-primary;
		width: 12px;
		height: auto;
		margin-left: 5px;
	}

	.attachment-list {
		display: block;
		flex-wrap: wrap;
		.list-file-item {
			height: 56px;
			padding: 12px;
			display: inline-flex;
			width: 217px;
			margin: 8px 8px 0 0;
			div:first-child {
				@include justify-content(flex-start);
			}
			div svg.file-icon {
				width: 24px;
				height: 24px;
				margin-top: 0;
			}
			.delete-file {
				margin: 0;
				padding: 0 0 0 8px;
			}
			&:hover {
				.delete-file {
					opacity: 1;
				}
			}
			.file-info {
				margin: 0 0 0 10px;
				width: 66%;
				.file {
					max-width: 100%;
					top: 5px;
					cursor: pointer;
					.file-name {
						font-weight: $text-weight-medium;
					}
				}
			}
		}
	}
	.relationship-map-wrapper {
		.map-alert {
			width: auto;
		}
	}
}
