.associated-ticket.incident-card {
	@import "../../form-components/priority-field";
	@import "../tickets/ticket-state";
	@import "../../ui-components/common/badges";
	&:hover {
		background: none;
	}

	.ember-badge {
		max-width: 144px;
		text-align: center;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		margin-right: 8px;

		&:hover {
			max-width: none;
		}
	}

	.incident-card-icon-ticket {
		margin-left: 0;
	}

	.incident-properties {
		width: 156px;
		flex-direction: row;
		align-items: center;
		border-left: 1px solid $border-light-color;
		padding-left: 16px;
		margin-left: auto;

		.label-value-field {
			@include flexbox();

			.label-field {
				min-width: 50px;
				max-width: 50px;
				color: $secondary-text-color-2;
				@include flexbox();
			}

			.option-field {
				padding-left: 12px;
				min-width: 50px;
				max-width: 100px;
				@include flexbox();
				font-weight: $text-weight-medium;
			}

			.priority-option-color {
				margin-right: 4px;
				margin-top: 5px;
			}
		}
	}
}
