&.services-list-layout {
	.service-list-table {
		.cell_default_service_name_item {
			@include line-clamp();
			.service-name-link {
				color: $primary-text-color-1;
				font-weight: $text-weight-semi-bold;
			}
		}

		.cell_default_impact_item {
			text-transform: capitalize;
		}

		.cell_default_managed_by_group_item {
			font-weight: $text-weight-semi-bold;
		}
	}

	.page-content-with-sidebar .services-table-wrapper .table-options .right-section {
		margin-right: 38px;
	}
	.services-table-wrapper {
		&.no-filter .table-options .right-section {
			margin-right: 4px;
		}
		.table-options {
			margin-left: -32px;
			padding-left: 10px;
			.left-section #bulk-actions-toolbar {
				padding-left: 4px;
				.search-input-wrapper {
					&:not(.active) {
						svg {
							fill: $disabled-text-color;
						}
					}
					.service-list-search-input {
						width: 320px;
					}
				}
			}
			.right-section {
				margin-right: 38px;
			}
			.btn-primary {
				background: $btnPrimaryBackground;
	
				&:hover {
					background-color: $btnPrimaryBackgroundHighlight;
				}
			}
		}
		.service-column-customizer {
			top: 0px;
		}
	}

	#view-query-form-trigger {
		position: fixed;
		top: 64px; // initial value, dynamic value set later in code based on a window event
		z-index: 999;
		right: 32px;
		margin-right: 0;
		margin-top: 14px;
		background: $app-white;
		&:focus {
			outline: none;
			box-shadow: 0 0 1px 1px $inputFocusBorder !important;
		}
		&.active {
			right: 332px;
			background: $section-background-blue;
			border: 1px solid $inputFocusBorder !important;
			border-radius: $inputBorderRadius;
			box-shadow: none;
			svg path {
				fill: $inputFocusBorder;
			}
		}
	}

	#default-state-container {
		color: $app-grey;
		.no-result {
			.empty-message {
				font-weight: $text-weight-semi-bold;
				@include font-size($text-large);
				margin: 24px 0 8px;
			}
			.hint-text {
				@include font-size($title-small);
			}
		}
		.title {
			@include font-size($text-large);
			font-weight: $text-weight-semi-bold;
			margin-top: 24px;
		}
		.empty-msg {
			@include flex-center;
			.empty-state-msg {
				@include font-size($title-small);
				margin: 8px 0;
				width: 70%;
			}
		}
		.empty-actions {
			@include flex-center;
			column-gap: 8px;
			margin-top: 24px;
		}
	}

	//Sidebar filter css
	.module-view-query-form {
		// Reset the css when date range component opened. 
		.calendar-open {
			.module-view-query-form-header, .module-view-query-form-search, & + .module-view-query-form-footer {
				z-index: unset !important;
			}
		}
	}
}
