.service-delete-modal {
	.delete-title {
		@include font-size(20px);
		font-weight: $text-weight-semi-bold;
	}
	.delete-desc {
		@include font-size(14px);
		color: $secondary-text-color-1;
	}
	.delete-section-title {
		@include font-size(14px);
		font-weight: $text-weight-semi-bold;
		margin-bottom: 24px;
		padding-right: 32px;
	}
	.integration-tile {
		@include flexbox;
		@include align-items(center);
		margin-bottom: 8px;

		.logo-section {
			width: 48px;
			height: 48px;
			background: $app-white;
			border: 1px solid $border-light-color;
			border-radius: $radius;
			margin-right: 16px;
			text-align: center;
			@include align-items(center);
			@include justify-content(center);
			@include flexbox;
			img {
				width: 24px;
				height: 24px;
			}
			&.custom-integration,
			&.email {
				.logo-wrapper {
					padding: 4px;
					background: $app-icon-color;
					border-radius: 50%;
					@include flexbox;
				}
			}
		}
		.content-section {
			.integration-title {
				@include font-size(14px);
				font-weight: $text-weight-semi-bold;
				padding-right: 32px;
			}
			.integration-desc {
				@include font-size(12px);
				color: $secondary-text-color-1;
			}
		}
	}
	&.ember-modal-dialog .ember-modal-body {
		padding-right: 0px;
	}
}
