// Service status
$service-status: needs_attention, operational; // needs_attention-1, operational-2

// Service status font colors
$service-status-color: (
	needs_attention: $alertPartialColor,
	operational: $alertSuccessColor,
);

// Service status bg colors
$service-status-bg: (
	needs_attention: $alertPartialBackground,
	operational: $alertSuccessBackground,
);

// Service status border colors
$service-status-border: (
	needs_attention: $alertPartialBorder,
	operational: $alertSuccessBorder,
);

@each $status in $service-status {
	.service-status-#{$status} {
		color: map-get($service-status-color, $status) !important;
		background: map-get($service-status-bg, $status) !important;
		border: 1px solid map-get($service-status-border, $status) !important;
	}
	.service-health-status-#{$status} {
		background: map-get($service-status-color, $status);
	}
}
.loader-container {
	@include flexbox;
	@include align-items(center);

	svg {
		fill: $app-primary;
		height: 28px;
		width: 28px;
	}
}
